@import url(https://fonts.googleapis.com/css2?family=Padauk:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(19,16,15);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.header{
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    right: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    color: white;
    font-family: 'Tangerine';
}

.logo {
    font-size: 55px;
    padding: 20px 40px;
    transition: all ease 0.5s;
    font-weight: bold;
    cursor: pointer;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
}

.logo:hover {
    -webkit-transform: scale(1);
            transform: scale(1);
}

.buttons {
    display: inline-block;
    margin-right: 30px;
    font-size: 35px;
    
}

.buttons div{
    display: -webkit-inline-flex;
    display: inline-flex;
    margin-right: 30px;
    transition: all ease 0.5s;
    cursor: pointer;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
}

.buttons div:hover {
    -webkit-transform: scale(1);
            transform: scale(1);
}
.container{
    margin-top: 100px;
    display: -webkit-inline-flex;
    display: inline-flex;
    
    width: 100%;
    overflow:auto;
    font-family: 'Tangerine';
}
.vertical--shadow {
    display: -webkit-inline-flex;
    display: inline-flex;
}

.container img{
    width: 100%;
    object-fit: cover;
    height: 60vh; 
}

.left, .right {
    position: relative;
    width: 50%;
    cursor:pointer;
}

.right {
    position: relative;
    top:0;
    left: 0;
    overflow: hidden;
    cursor:pointer;
}
.right--text{
    font-size: 50px;
    color: white;
    position: absolute;
    transition: none;
    margin-top: 25%;
    width: 40%;
    text-align: center;
    margin-left: 5%
}
.left--text{
    font-size: 50px;
    color: white;
    position: absolute;
    transition: none;
    margin-top: 25%;
    width: 40%;
    text-align: center;
    float: left;
    margin-left: 55%;
}
.left {

}

.img--left--Back{
    
}
.left:hover .img--left--Back {
    opacity: 0.3;
    transition: all ease 0.7s;
    -webkit-transform: scale(0.99);
            transform: scale(0.99);
}
.left:hover .img--left--Front {
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
    opacity: 1;
}

.img--right--Front {
    position: absolute;
    transition: all ease 0.7s;
}

.right:hover .img--right--Back {
    opacity: 0.3;
    transition: all ease 0.7s;
    -webkit-transform: scale(0.99);
            transform: scale(0.99);
}
.right:hover .img--right--Front {
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
    opacity: 1;
}
.movieRow {
    margin-bottom: 30px;
    color: rgba(255,255,255,0.6);
    overflow-x: hidden;
    margin-top: 50px;
    vertical-align: middle;
    
}

.movieRow h2 {
    margin: 0px 0px 10px 30px;
}

.movieRow--listarea {
    overflow-x: hidden;
    padding-left: 30px;
    display:inline-block;
    margin-top: 30px;
}

.movieRow--list{
    transition: all ease 0.5s;
    text-align: center;
    margin-left: -40px;
}

.movieRow--item{
    display:inline-block;
    width: 180px;
    cursor: pointer;
    margin-left: 40px;
}

.movieRow--item img {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
    transition: all ease 0.3s;
    font-size: 12px;
    max-width: 120px;
    height: 250px;
    object-fit: cover;
    text-align: center; 
    margin: auto;
    display: block;
}

.movieRow--item img:hover{
    -webkit-transform: scale(1);
            transform: scale(1);
} 

.movieRow--left,
.movieRow--right {
    margin-top: 50px;
    position: absolute;
    width: 40px;
    height: 225px;
    background-color: rgba(0,0,0,0.6);
    z-index:99;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    overflow: hidden;
    cursor: pointer;
    opacity: 0;
    transition: all ease 0.5s;
}
.movieRow--left {
    left: 0;
}
.movieRow--right {
    right: 0;
}
.movieRow:hover .movieRow--left,
.movieRow:hover .movieRow--right {
    opacity: 1;
}

@media (max-width:760px) {
    .movieRow--left,
    .movieRow--right {
        opacity: 1;
    }
}

.Line {
    border: 0.5px solid rgba(255,255,255,0.5);
}
.Card {
    color: rgba(255,255,255,0.6);
    text-align: center;
    font-family: 'Padauk', sans-serif;
}

.Img--Card {
    position: relative;
}

.square {
    width:100%;
    height:50%;
    background-color: rgba(128,1,63, 0.2);
    position: absolute;
    opacity: 0;
    transition: all ease 0.4s;
    overflow-x: hidden;
    -webkit-transform: scale(0);
            transform: scale(0);
}

.Card:hover .square{
    opacity:1;
    -webkit-transform: scale(1);
            transform: scale(1);
}

.Card h3 {
    text-align:center;
    height: 80px;
    padding: 5px;
    margin: 0px -10px 0px -10px;
    font-size: 18px;
    line-height: 20px;
}

.Value {
    margin-top: 5px;
    font-size: 14px;
    text-decoration: line-through;
}

.Promo {
    margin-top: -20px;
    font-size: 24px;
    color: rgb(19,16,15);
    background-color: rgba(255,255,255,0.8);
    margin: 0 20px 0 20px;
    transition: all ease 0.4s;
    position: relative;
}

.Promo:hover .Img--Card{
    -webkit-transform: scale(2);
            transform: scale(2);
}

.Promo h4::before {  
    -webkit-transform: scaleX(0);  
            transform: scaleX(0);
    -webkit-transform-origin: bottom right;
            transform-origin: bottom right;
}

.Promo h4:hover::before {
-webkit-transform: scaleX(1);
        transform: scaleX(1);
-webkit-transform-origin: bottom left;
        transform-origin: bottom left;
}

.Promo h4::before {
    content: " ";
    display: block;
    position: absolute;
    top: 0; right: 0; bottom: 0; left: 0;
    inset: 0 0 0 0;
    background: hsl(200 100% 80%);
    z-index: -1;
    transition: -webkit-transform .5s ease;
    transition: transform .5s ease;
    transition: transform .5s ease, -webkit-transform .5s ease;
}

@media (orientation: landscape) {
body {
    grid-auto-flow: column;
}
}
  

.Main {
    width: 100%;
    height: 100vh;
    margin-bottom: 1000px;
    font-family: 'Tangerine';
}

.Main--Image{
    height: 70%;
    -webkit-transform:scale(1);
            transform:scale(1);
    transition: all ease 0.5s;
    opacity: 0.8;
    object-fit: contain;
    overflow: hidden;
}

.Main--Image:hover{
    
}

.Background {
    height: 100vh;
}

.Background--vertical {
    width: inherit;
    height: inherit;
    background: linear-gradient(to bottom, rgb(19,16,15) 10%, transparent 40%);
}

.Main--Background {
    position: absolute;
    width: 70%;
}

.Background--horizontal {
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, rgb(19,16,15) 30%, transparent 130%);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}

.Text{ 
    color: white;
    position: absolute;
    margin-top: 30%;
}

.Title {
    color: white;
    font-size: 90px;
    margin: 200px 0px 10px 100px;
}

.Content {
    font-size: 24px;
    font-family: 'Roboto', sans-serif;
    max-width: 35%;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 0px 0px 800px 100px;
    display: -webkit-flex;
    display: flex;
}
